import axios, { AxiosError, AxiosRequestConfig, AxiosResponse } from "axios";
import { renewToken } from "./users/usersApi";

const API = axios.create({
  baseURL: process.env.REACT_APP_SERVER,
  withCredentials: true,
});

export const getLocation = async () => {
  const { data } = await axios.get(`https://ipinfo.io/?token=${process.env.REACT_APP_IPINFO_TOKEN}`);
  return data.loc.split(",") as string[];
};

let isRefreshing = false; // Token refresh   
let failedQueue: Array<{
  resolve: (token: string) => void;
  reject: (error: AxiosError) => void;
}> = [];
//   

const processQueue = (
  error: AxiosError | null,
  token: string | null = null
) => {
  failedQueue.forEach(({ resolve, reject }) => {
    if (token) {
      resolve(token);
    } else {
      reject(error!);
    }
  });
  failedQueue = [];
};

API.interceptors.response.use(
  (response: AxiosResponse) => response,
  async (error: AxiosError) => {
    const originalRequest = error.config as AxiosRequestConfig & {
      _retry?: boolean;
      _retryCount?: number;
    };

    if (error.response?.status === 401) {
      if (originalRequest._retry) {
        //    ,    
        return new Promise<string>((resolve, reject) => {
          failedQueue.push({ resolve, reject });
        })
          .then((token: string) => {
            if (!originalRequest.headers) originalRequest.headers = {};
            originalRequest.headers["Authorization"] = `Bearer ${token}`;
            return API(originalRequest);
          })
          .catch((err: AxiosError) => Promise.reject(err));
      }

      //   
      originalRequest._retry = true;
      originalRequest._retryCount = (originalRequest._retryCount || 0) + 1;

      if (originalRequest._retryCount > 2) {
        return Promise.reject(error); //      
      }

      if (isRefreshing) {
        //     ,    
        return new Promise<string>((resolve, reject) => {
          failedQueue.push({ resolve, reject });
        })
          .then((token: string) => {
            if (!originalRequest.headers) originalRequest.headers = {};
            originalRequest.headers["Authorization"] = `Bearer ${token}`;
            return API(originalRequest);
          })
          .catch((err: AxiosError) => Promise.reject(err));
      }

      isRefreshing = true;

      try {
        const { accessToken } = await renewToken();

        // API        
        API.defaults.headers.common["Authorization"] = `Bearer ${accessToken}`;
        if (!originalRequest.headers) originalRequest.headers = {};
        originalRequest.headers["Authorization"] = `Bearer ${accessToken}`;

        processQueue(null, accessToken);

        //   
        return API(originalRequest);
      } catch (tokenError) {
        processQueue(tokenError as AxiosError, null);
        return Promise.reject(tokenError);
      } finally {
        isRefreshing = false;
      }
    }

    return Promise.reject(error);
  }
);

export default API;
